.theme__switch-wrapper {
  animation: pulseSwitch 3s ease-in-out infinite;
}

@keyframes pulseSwitch {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

.dark .theme__switch-wrapper {
  animation: pulseSwitch 3s ease-in-out infinite;
}

.theme__switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.theme__switch input {
  display: none;
}

.slider {
  cursor: pointer;
  background: linear-gradient(#4f2ee8, #dcdcdc);
  border-radius: 34px;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 1px 1px rgba(0, 0, 0, .1), inset -1px -1px rgba(0, 0, 0, .1), 0 0 4px #4f2ee8;
}

.slider:before {
  content: " ";
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: all .4s;
  position: absolute;
  top: 4px;
  left: 1px;
  transform: translate(-1px, -3px);
}

input:checked + .slider, input:focus + .slider {
  background: linear-gradient(#4f2ee8, #686868);
  box-shadow: inset 1px 1px rgba(0, 0, 0, .1), inset -1px -1px rgba(0, 0, 0, .1), 0 0 4px #4f2ee8;
}

input:checked + .slider:before {
  height: 18px;
  width: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  top: 4px;
  left: 22px;
  transform: translate(-1px, -3px);
}

.dark .header-wrapper, .dark .shopping__item {
  background-color: #111;
  border: 1.5px solid #fff;
  border-radius: 0 0 8px 8px;
}

.dark .shopping__item {
  border: 1.5px solid #2a1d67;
