// Igor
.header {
    position: fixed;
}

.header-wrapper {
    display: flex;
    position: relative;
    border: 1.5px solid #111111;
    border-radius: 0px 0px 8px 8px;
    background-color: $color-background-text-white;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), color 250ms cubic-bezier(0.4, 0, 0.2, 1), border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.mob-header {
    @media screen and (min-width: 375px) {
        width: 375px;
    }
}

//  ЛОГОТИП
.header__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    padding-top: 18px;
    padding-bottom: 18px;
    margin-right: 145px;
    color: #111111;
    @media screen and (min-width: 768px) {
        padding-top: 19px;
        padding-bottom: 25px;
    }
}

// МЕНЮ ДЛЯ АВТОРИЗОВАНОГО КОРИСТУВАЧА
.menu {
    display: none;
    margin-left: 32px;
    align-items: center;
    gap: 24px;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: -0.04em;
    text-transform: uppercase;
    color: $color-background-text-black;
    transition: opacity cubic-bezier(0.4, 0, 0.2, 1), visibility cubic-bezier(0.4, 0, 0.2, 1);
    @media screen and (min-width: 768px) {
        display: flex;
    }
    @media screen and (min-width: 1440px) {
        margin-left: 99px;
    }
    &.is-hidden {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
    }
}

.menu__shopping {
    display: flex;
    gap: 4px;
    align-items: center;
    color: #111111;
}

.icon-cart {
    fill: #111111;
}

.current {
    font-weight: 700;
    background: #EAC645;
    border-radius: 36px;
    padding: 8px 16px;
    color: #111111;
}

//  КНОПКА БУРГЕР МЕНЮ МЕНЮ
.menu-toggle {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    cursor: pointer;
    border: none;
}

@media (min-width: 768px) {
    .menu-toggle {
        display: none;
    }
}

.icon-burger {
    fill: #111111;
}

.icon-href {
    color: #111111;
}

.menu-toggle :hover,
.menu-toggle :focus {
    fill: #4F2EE8;
}

// ПЕРЕМИКАЧ ТЕМНОІ ТЕМИ ТА  КНОПКИ  "sign-up", "user"
.header__group {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 14px;
    margin-left: auto;
    @media screen and (min-width: 768px) {
        gap: 24px;
    }
}

//  КНОПКИ  "sign-up" ТА  "user"
.auth__modal-open {
    display: none;
    align-items: center;
    justify-content: center;
    gap: 90px;
    width: 184px;
    height: 48px;
    background-color: #4f2ee8;
    border: 1.5px solid #111111;
    border-radius: 18px;
    color: #ffffff;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    transition: opacity cubic-bezier(0.4, 0, 0.2, 1), visibility cubic-bezier(0.4, 0, 0.2, 1);
    @media screen and (min-width: 768px) {
        display: flex;
    }
    &.is-hidden {
        display: none;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
    }
}

.auth__modal-open-js:hover,
.auth__modal-open-js:focus {
    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.3);
}

.authorised-btns__wrapper {
    position: relative;
    &.is-hidden {
        display: none;
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
    }
}

.btn-user {
    display: none;
    align-items: center;
    justify-content: space-around;
    gap: 5px;
    font-weight: 700;
    font-size: 22px;
    line-height: 29px;
    letter-spacing: -0.02em;
    color: #ffffff;
    border: 1px solid #4f2ee8;
    border-radius: 36px;
    background-color: #4f2ee8;
    height: 45px;
    @media screen and (min-width: 768px) {
        display: flex;
    }
}

.fone-icon-user {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    background-color: #f6f6f6;
    width: 37px;
    height: 37px;
    border-radius: 50%;
}

.icon-user {
    fill: #4f2ee8;
}

.icon-triangle-down {
    fill: #ffffff;
}

.btn-user:hover,
.btn-user:focus {
    box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.3);
}

// ==============
.log-out-btn {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 90px;
    width: 184px;
    height: 48px;
    background-color: #ffffff;
    border: 1.5px solid #111111;
    border-radius: 18px;
    color: #111111;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    @media screen and (max-width: 768px) {
        display: none;
    }
}

.log-out-btn.is-hidden {
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
}

