// DARK_THEME
.theme__switch-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 3;
    right: 232px;
    top: 24px;
    border-radius: 34px;
}

@media screen and (max-width: 768px) {
    .theme__switch-wrapper {
        right: 240px;
    }
}

@media screen and (max-width: 320px) {
    .theme__switch-wrapper {
        right: 62px;
        top: 20px;
    }
}

.theme__switch-wrapper {
    animation: pulseSwitch 3s ease-in-out infinite;
}

@keyframes pulseSwitch {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}

.dark .theme__switch-wrapper {
    animation: pulseSwitch 3s ease-in-out infinite;
}

@keyframes pulseSwitch {
    0% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
    100% {
        transform: scale(1);
    }
}

.theme__switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 20px;
}

.theme__switch input {
    display: none;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: linear-gradient( to bottom, rgba(79, 46, 232, 1), rgba(220, 220, 220, 1));
    box-shadow: inset 1px 1px rgba(0, 0, 0, 0.1), inset -1px -1px rgba(0, 0, 0, 0.1), 0 0 4px rgba(79, 46, 232, 1);
    border-radius: 34px;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: ' ';
    height: 18px;
    width: 18px;
    top: 4px;
    left: 1px;
    background-color: #ffffff;
    transform: translate(-1px, -3px);
    background: linear-gradient(to bottom, #ffffff 0%, #f7f7f7 100%);
    border-radius: 50%;
    transition: 0.4s;
}

input:checked+.slider {
    background: linear-gradient( to bottom, rgba(79, 46, 232, 1), rgba(104, 104, 104, 1));
    box-shadow: inset 1px 1px rgba(0, 0, 0, 0.1), inset -1px -1px rgba(0, 0, 0, 0.1), 0 0 4px rgba(79, 46, 232, 1);
}

input:focus+.slider {
    background: linear-gradient( to bottom, rgba(79, 46, 232, 1), rgba(104, 104, 104, 1));
    box-shadow: inset 1px 1px rgba(0, 0, 0, 0.1), inset -1px -1px rgba(0, 0, 0, 0.1), 0 0 4px rgba(79, 46, 232, 1);
}

input:checked+.slider:before {
    height: 18px;
    width: 18px;
    top: 4px;
    left: 22px;
    transform: translate(-1px, -3px);
    background: linear-gradient(to bottom, #ffffff 0%, #f7f7f7 100%);
    border-radius: 50%;
}

